import { getPageData, getPageSlugs } from '@/lib/cms';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import rehypeRaw from 'rehype-raw';
import { notFound } from 'next/navigation';
import { Metadata } from 'next';
import styles from './page.module.css';
import HeroSection from '@/components/HeroSection';
import ServiceAreas from '@/components/ServiceAreas';
import CTA from '@/components/CTA';
import SetsApart from '@/components/SetsApart';
import ServiceGrid from '@/components/ServiceGrid';
import ProcessSteps from '@/components/ProcessSteps';


interface PageProps {
    params: Promise<{
        slug: string[];
    }>;
}

export async function generateStaticParams() {
    const slugs = getPageSlugs();
    return slugs.map((slug) => ({
        slug: [slug],
    }));
}

export async function generateMetadata({ params }: PageProps): Promise<Metadata> {
    const resolvedParams = await params;
    const slug = resolvedParams.slug.join('/');
    const data = getPageData(slug);

    if (!data) return {};

    return {
        title: data.title,
        description: data.description || `RWF Home Improvements - ${data.headings?.[0]?.text}`,
    };
}

export default async function DynamicPage({ params }: PageProps) {
    const resolvedParams = await params;
    const slug = resolvedParams.slug.join('/');
    const data = getPageData(slug);

    if (!data || !data.content) {
        notFound();
    }

    // Find H1
    const h1 = data.headings?.find((h: any) => h.tag === 'h1')?.text || data.title;

    // Determine Image: heroImage > ogImage > Default
    const defaultHero = "/images/expert-fence-installation-fayetteville-nc.png";
    let heroBg = data.heroImage;
    if (!heroBg && data.ogImage && !data.ogImage.includes('wixstatic')) {
        // Use ogImage if it's local, otherwise prefer default high-quality hero over random low-res wix scrape
        heroBg = data.ogImage;
    }
    if (!heroBg) {
        heroBg = defaultHero;
    }

    return (
        <main>
            <HeroSection
                backgroundImage={heroBg}
                title={h1}
                subtitle="Trusted Quality & Craftsmanship" // Fallback subtitle for uniformity
                ctaLink="/contact-us"
                ctaText="Get a Quote"
            />

            <div className="container">
                <article className={styles.content}>
                    <ReactMarkdown
                        remarkPlugins={[remarkGfm]}
                        rehypePlugins={[rehypeRaw]}
                        components={{
                            img: ({ node, ...props }) => (
                                <img {...props} />
                            ),
                        }}
                    >
                        {data.content}
                    </ReactMarkdown>
                </article>
            </div>

            {data.features && (
                <SetsApart
                    title={data.featuresTitle}
                    items={data.features}
                />
            )}

            {data.financingOptions && (
                <section className="container py-5" style={{ textAlign: "center", paddingBottom: 0 }}>
                    <h2 style={{
                        fontSize: "2.5rem",
                        fontWeight: "800",
                        marginBottom: "3rem",
                        color: "#333"
                    }}>
                        Financing Options That Work for You
                    </h2>
                    <ServiceGrid services={data.financingOptions} />
                </section>
            )}

            {data.processSteps && (
                <ProcessSteps
                    title={data.processTitle || "Our Fencing Installation Process"}
                    steps={data.processSteps}
                />
            )}

            <ServiceAreas />

            <CTA />
        </main>
    );
}
