
import Link from 'next/link';
import styles from './CTA.module.css';

export default function CTA() {
    return (
        <section className="container">
            <div className={styles.ctaSection}>
                <div className={styles.ctaImageCol}>
                    <img
                        src="/images/rwf-home-improvements-team-richard-wilkes-fayetteville-nc.jpeg"
                        alt="Richard Wilkes, Owner of RWF Home Improvements"
                        className={styles.ctaOwnerImage}
                    />
                </div>
                <div className={styles.ctaContentCol}>
                    <h2>Ready to Transform Your Home?</h2>
                    <p>
                        Elevate your home's curb appeal and functionality with RWF Home Improvements. From <strong>custom fencing solutions</strong> that provide privacy and security to <strong>stunning decks</strong> for outdoor entertaining, and <strong>energy-efficient windows and doors</strong> that save you money, we deliver craftsmanship you can trust. Let's start your project today!
                    </p>
                    <Link href="/contact-us" className={styles.ctaButton}>
                        Call now
                    </Link>
                </div>
            </div>
        </section>
    );
}
